@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);
* {
  margin: 0;
  padding: 0;
  font-family: 'Poppins', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
}

.header {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    width: 90%;
    margin: auto;
    display: flex;
    align-items: center;
}

.header a {
    color: black;
    text-decoration: none;
    font-weight: bold;
    font-size: 1vmax;
    margin: 40px 20px;
}

@media (max-width: 500px) {
    .header a { font-size: 2vmax; }
}
@media (max-width: 900px) and (orientation: landscape)  {
    .header a { font-size: 2vmax; }
}
/* ----- ANIMATIONS ----- */
@-webkit-keyframes fadeInDesktop {
    0% { position: absolute; top: 60%; opacity: 0; }
    100% { position: absolute; top: 16%; opacity: 1; }
}
@keyframes fadeInDesktop {
    0% { position: absolute; top: 60%; opacity: 0; }
    100% { position: absolute; top: 16%; opacity: 1; }
}

@-webkit-keyframes fadeInPhone {
    0% { position: absolute; top: 60%; opacity: 0; }
    100% { position: absolute; top: 10%; opacity: 1; }
}

@keyframes fadeInPhone {
    0% { position: absolute; top: 60%; opacity: 0; }
    100% { position: absolute; top: 10%; opacity: 1; }
}


/* ----- LANDING PAGE ----- */
#landing_page {
    height: 100vh;
    background: url(/static/media/background2.24f0eaed.jpg);
    background-repeat: no-repeat;
    background-position: top right;
    background-size: cover;
    background-attachment: fixed;
}

#landing_page_content {
    position: absolute;
    top: 16%;
    left: 5%;
    padding: 0 3%;
    -webkit-animation-name: fadeInDesktop;
            animation-name: fadeInDesktop;
    -webkit-animation-duration: 1.5s;
            animation-duration: 1.5s
}

#landing_page_content_title {
    width: 70%;
    font-size: 5vmax;
    font-weight: bold;
    color: #524B47;
    line-height: 5.5vmax;
    
    /* -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: black; */
}

#landing_page_content_text {
    width: 60%;
    font-size: 2vmax;
    color: #4C535B;
}



@media (max-width: 500px) {
    #landing_page { background-position: top left; }
    #landing_page_content { top: 10%; -webkit-animation-name: fadeInPhone; animation-name: fadeInPhone; }
    #landing_page_content_title { text-align: center; margin: auto; }
    #landing_page_content_text { font-size: 1.25em; width: unset; text-align: center; }
}
@media (max-width: 900px) and (orientation: landscape)  {
    #landing_page_content_title { font-size: 3em; }
}

.boxes {
    width: 100%;
    font-size: 2vmax;
}

.row {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.row a {
    text-decoration: none;
}
.box_row a {
    text-decoration: none;
}

.box_row {
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;

    position: relative;
    width: calc(100% / 3);
    padding-top: calc(100% / 4); /* 4 to 3 for 1:1 */
}

.box_row .text {
    position:  absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;

    display: flex;
    justify-content: center;
    align-items: center;
}

.box_row p, .box_text p {
    background: white;
    color: black;
    padding: 20px;
    font-size: 2vmax;
    text-align: center;
    font-weight: bold;
}

.box_text {
    width: calc(100% / 3);
    padding: 3% 0;
}

.box_text p {
    text-align: center;
    font-size: 1.75vmax;
    font-weight: normal;
    background: none;
}

@media (max-width: 1100px) {
    .box_row { width: 100%; padding-top: 60%; }
    .box_text { width: 100%; }
}
.footer {
    margin-top: 5%;
    width: 100%;
    height: 100px;
    background: darkgrey;
    font-size: 1vmax;

    display: flex;
    justify-content: center;
    align-items: center;
}

@media (max-width: 500px) {
    .footer { font-size: 1.5vmax; }
}
@media (max-width: 900px) and (orientation: landscape)  {
    .footer { font-size: 1.5vmax; }
}



ul {
    list-style: circle;
    list-style-position: inside;
}

.content {
    width: 50%;
    margin: auto;
    margin-top: 100px;
    padding: 5%;
    font-size: 1.75vmax;
}

.content::first-letter {
    font-size: 300%;
    line-height: 0;
}

@media (max-width: 500px) {
    .content { width: 90%; font-size: 20px; }
}
.back {
    width: 100%;
    margin: auto;
    text-align: center;
    font-size: 30px;
    font-weight: bold;
}

a {
    text-decoration: none;
}

@media (max-width: 500px) {
    .content { width: 90%; font-size: 20px; }
    .back { font-size: 20px; }
}
